.products {
    background-color: rgb(237, 238, 255);
    padding: 10vh 0;
    margin: 5vh 0;
    .products__list-container {
        margin: auto;
        width: 92.5%;
        display: flex;
        align-items: center;
        background-color: rgb(255, 255, 255);
        color: #000;
        border-radius: 14px;
        flex-wrap: wrap;
        .products__nav {
            line-height: calc(1.5rem * 1.4);
            font-size: 1.5rem;
            text-transform: uppercase;
            font-weight: 500;
            padding: 1.25rem 0.25rem;
            width: 100%;
            justify-content: space-between;
            display: flex;
            flex-direction: row;
            text-align: left;
            .products_category {
                width: calc(85%/3);
                &:last-of-type {
                    width: 15%;
                }
            }
            .expiration-date {
                display: flex;
                align-items: center;
                justify-content: center;
                cursor: pointer;
                .arrow-icon {
                    margin-top: 0.21rem;
                    margin-left: 0.125rem;
                }
            }
        }
        .product__row {
            line-height: calc(1.1rem * 1.25);
            font-size: 1.1rem;
            word-spacing: -1px;
            padding: 1rem 0.25rem;
            width: 100%;
            justify-content: space-between;
            display: flex;
            align-items: center;
            flex-direction: row;
            text-align: left;
            border-top: 1px solid rgb(190, 190, 190);
            li {
                width: calc(85%/3);
                &:last-of-type {
                    width: 15%;
                }
            }
            .proudctName-row {
                text-transform: capitalize;
            }
            .category-row {
                margin: 0 auto;
                width: 40%;
                display: flex;
                align-items: center;
                justify-content: space-around;
                .cathegory-thumbnail {
                    height: 37.5px;
                    width: 37.5px;
                    user-select: none;
                }
            }
        }
    }
}